
@import url("https://fonts.googleapis.com/css?family=Righteous");
@import url("https://fonts.googleapis.com/css?family=Concert+One");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --postLinkH: 20vh;
  --mainFont: 'Righteous', cursive;
  --secFont: 'Concert One', cursive;
}
.posts__list {
  width: 60%;
  height: auto;
  padding: 9vh 0 9vh 0;
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: reverse;
      -ms-flex-flow: column-reverse wrap;
          flex-flow: column-reverse wrap;
}
.posts__list .post__link {
    width: 100%;
    min-height: 15vh;
    height: auto;
    max-height: 60vh;
    border-bottom: 1.5px solid #1b1b1b;
    margin-bottom: 3vh;
    will-change: height;
}
.posts__list .post__link .post__link__title {
      display: block;
      font-size: calc(var(--postLinkH) / 4);
      width: 40%;
      height: auto;
      min-height: 20%;
      max-height: 40%;
      font-weight: 800;
      font-family: var(--mainFont);
      text-transform: capitalize;
      color: #1b1b1b;
      text-decoration: none;
      will-change: text-decoration;
      transition: -webkit-transform .5s 0s;
      transition: transform .5s 0s;
      transition: transform .5s 0s, -webkit-transform .5s 0s;
}
.posts__list .post__link .post__link__title:hover, .posts__list .post__link .post__link__title:focus {
        transition: text-decoration .5s 0s, -webkit-transform .5s 0s;
        transition: transform .5s 0s, text-decoration .5s 0s;
        transition: transform .5s 0s, text-decoration .5s 0s, -webkit-transform .5s 0s;
        -webkit-transform: translateX(-2%);
                transform: translateX(-2%);
        text-decoration: underline #00ffff;
        outline: none;
}
.posts__list .post__link .post__link__date {
      width: 100%;
      height: auto;
      max-height: 10%;
      font-size: calc(var(--postLinkH) / 7);
      color: #3c3c3c;
      font-weight: 800;
      padding-left: 1%;
}
.posts__list .post__link .post__link__text {
      width: 100%;
      height: auto;
      min-height: 40%;
      max-height: 50%;
      font-size: calc(var(--postLinkH) / 6);
      padding-left: 2%;
}
.posts__list .post__link .post__text__extend {
      --h: 4vh;
      width: 100%;
      height: var(--h);
      font-size: calc(var(--h) / 1.1);
      text-align: center;
      color: #3c3c3c;
      background-color: transparent;
      border: none;
      transition: color .5s 0s;
      margin: .5vh 0 1vh 0;
}
.posts__list .post__link .post__text__extend:hover, .posts__list .post__link .post__text__extend:focus {
        transition: color .5s 0s;
        color: #00ffff;
        outline: none;
}



