
@import url("../css/fontello.css");
@import url("https://fonts.googleapis.com/css?family=Righteous");
@import url("https://fonts.googleapis.com/css?family=Concert+One");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.menu {
  z-index: 5000;
}
@media only screen and (min-width: 1367px) {
:root {
    --h: 5vh;
    --navItemSize: calc(var(--h) / 1.5);
    --LogofontSize: calc(var(--h) * 1.1);
    --mainFont: 'Righteous', cursive;
    --secFont: 'Concert One', cursive;
    --navItemfontSize: calc(var(--navItemSize) / 1.7);
    --lineHeight: calc(var(--h) + (var(--h) / 20));
    --navLineHeight: calc(var(--navItemSize) + (var(--navItemSize) / 20));
    --logoColor: #fff;
    --color: #00ffd0;
}
.menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: var(--h);
    background-color: #2c2c2c;
    border-bottom: 4px solid #3d3d3d;
}
.menu .logo {
      position: fixed;
      top: 0;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
}
.menu .logo .logo__link {
        color: var(--logoColor);
        transition: color 10s 0s;
        text-decoration: none;
        line-height: calc(var(--lineHeight) * 1.7);
        text-shadow: 2px 2px 4px #2c2c2c;
        font-size: var(--LogofontSize);
        font-family: var(--mainFont);
        will-change: text-shadow;
        transition: text-shadow .5s 0s;
}
.menu .logo .logo__link:hover, .menu .logo .logo__link:focus {
          outline: none;
          text-shadow: 2px 2px 4px #00ffff;
          transition: text-shadow .5s 0s;
}
.menu .nav {
      width: 40%;
      height: 100%;
}
.menu .nav .nav__links {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            -ms-flex-flow: row nowrap;
                flex-flow: row nowrap;
}
.menu .nav .nav__link {
        display: block;
        width: var(--navItemSize);
        height: var(--navItemSize);
        background-color: #fff;
        line-height: var(--navLineHeight);
        text-align: center;
        border-radius: 50%;
        margin: calc((var(--h) - var(--navItemSize)) / 2) 2%;
        box-shadow: 0px 0px 1px 2px #1b1b1b;
        will-change: border-radius;
        transition: border-radius .5s 0s, -webkit-transform .5s 0s;
        transition: transform .5s 0s, border-radius .5s 0s;
        transition: transform .5s 0s, border-radius .5s 0s, -webkit-transform .5s 0s;
        -webkit-transform-origin: 50% -50%;
                transform-origin: 50% -50%;
}
.menu .nav .nav__link:hover, .menu .nav .nav__link:focus {
          outline: none;
          -webkit-transform: translateY(10%) scale(1.2);
                  transform: translateY(10%) scale(1.2);
          border-radius: 0;
          transition: border-radius .5s 0s, -webkit-transform .5s 0s;
          transition: transform .5s 0s, border-radius .5s 0s;
          transition: transform .5s 0s, border-radius .5s 0s, -webkit-transform .5s 0s;
}
.menu .nav .nav__link:hover .nav__link__icon, .menu .nav .nav__link:focus .nav__link__icon {
            color: #00ffd0;
            transition: color .5s 0s;
}
.menu .nav .nav__link .nav__link__icon {
          color: #1b1b1b;
          font-size: var(--navItemfontSize);
          transition: color .5s 0s;
}
.menu .nav .active {
        outline: none;
        -webkit-transform: translateY(10%) scale(1.2);
                transform: translateY(10%) scale(1.2);
        border-radius: 0;
        will-change: border-radius;
        transition: border-radius .5s 0s, -webkit-transform .5s 0s;
        transition: transform .5s 0s, border-radius .5s 0s;
        transition: transform .5s 0s, border-radius .5s 0s, -webkit-transform .5s 0s;
}
.menu .nav .active .nav__link__icon {
          color: #00ffd0;
          transition: color .5s 0s;
}
}
